html {
  box-sizing: border-box;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-height: 100%;
  font-family: 'VT323', monospace;
  display: flex;
  flex-direction: column;
  font-size: 16px;
}

noscript {
  text-align: center;
  color: red;
}

header,
footer {
  padding: 2rem 0;
  text-align: center;
}

h1 {
  margin: 0 0 1rem;
}

p {
  margin: 0;
}

main {
  flex: 1;
  width: 100%;
  max-width: 800px;
  margin: auto;
  padding: 1rem;

  display: flex;
  justify-content: center;
  flex-direction: column;
}

canvas {
  margin-bottom: 3rem;
}

a {
  text-decoration: none;
  color: black;
}

.stats {
  display: flex;
  justify-content: space-around;
}
